import React, { useMemo } from "react";
import CollapsiblePanel from "../../../components/Tabs/CollapsiblePanel";
import TmsSettingsAccordionHeader from "./components/TmsSettingsAccordionHeader";
import { ReactComponent as CompanyIcon } from "../../../../assets/icons/shipment-file.svg";
import { ReactComponent as TruckIcon } from "../../../../assets/icons/truck.svg";
import { ReactComponent as DocumentIcon } from "../../../../assets/icons/docs-list.svg";
import { ReactComponent as FileFoldIcon } from "../../../../assets/icons/file-fold.svg";
import TmsCompanySettingsForm from "./components/TmsCompanySettingsForm";
import TmsShipmentSettingsForm from "./components/TmsShipmentSettingsForm";
import TmsAuctionSettingsForm from "./components/TmsAuctionSettingsForm";
import TmsPovSettingsForm from "./components/TmsPovSettingsForm";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../utilz/i18n';

const TmsSettingsScreen = ({ activeTab, setActiveTab }) => {
  const { pathname: activeURL } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeURL), [activeURL]);
  const { t } = useTranslation([namespace, 'translation']);
 
  const handleTabClick = (keys) => {
    setActiveTab(keys);
  };
  return (
    <CollapsiblePanel
      forceRender={true}
      handleTabClick={handleTabClick}
      activeKey={activeTab}
      tabs={[
        {
          header: (
            <TmsSettingsAccordionHeader
              Icon={CompanyIcon}
              title={t('translation:tms-settings.companySettings')}
            />
          ),
          content: <TmsCompanySettingsForm />,
        },
        {
          header: (
            <TmsSettingsAccordionHeader
              Icon={TruckIcon}
              title={t('translation:tms-settings.shipmentSettings')}
            />
          ),
          content: <TmsShipmentSettingsForm />,
        },
        {
          header: (
            <TmsSettingsAccordionHeader
              Icon={DocumentIcon}
              title={t('translation:tms-settings.auctionSettings')}
            />
          ),
          content: <TmsAuctionSettingsForm />,
        },
        {
          header: (
            <TmsSettingsAccordionHeader
              Icon={FileFoldIcon}
              title={t('translation:tms-settings.povSettings')}
            />
          ),
          content: <TmsPovSettingsForm />,
        },
      ]}
    />
  );
};

export default TmsSettingsScreen;
