import React, { useEffect, useMemo, useState } from "react";
import { getRatingPreferenceEffect } from "../../../redux/shipment/ShipmentEffects";
import FormSelectBox from "../Inputs/FormSelectBox";
import { useLocation } from "react-router-dom";
import { routeFileSelector } from "../../../utilz/i18n";
import { useTranslation } from "react-i18next";
import i18n from 'i18next';

const RatingPreferenceSelector = ({
  placeholder = i18n.t('translation:default.fields.ratingPreferences.placeholder'),
  label = i18n.t('translation:default.fields.ratingPreferences.label'),
  rules = [],
  name,
  margin,
  tooltip,
  isDefault = false,
}) => {
  const [options, setOptions] = useState([]);
  const [loading, setLoading] = useState(false);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  useEffect(() => {
    getStatusList();
  }, []);

  const getStatusList = async () => {
    try {
      setLoading(true);
      const { data } = await getRatingPreferenceEffect();
      let options = data.map((row) => ({
        label: row.codeDesc,
        value: row.codeId,
      }));
      setOptions(options);
      setLoading(false);
    } catch (error) {
      console.log(error);
      setOptions([]);
      setLoading(false);
    }
  };
  return (
    <FormSelectBox
      loading={loading}
      options={
        isDefault === true
          ? [{ label: t('translation:default.buttons.select'), value: "" }, ...options]
          : options
      }
      placeholder={placeholder}
      label={label}
      margin={margin}
      rules={rules}
      name={name}
      tooltip={tooltip}
    />
  );
};

export default RatingPreferenceSelector;
