import { Col, Form, Row } from "antd";
import React, { useEffect, useMemo } from "react";
import { useSelector } from "react-redux";
import {
  TMS_SETTINGS_POV_DESTINATION_OPTIONS,
  TMS_SETTINGS_POV_ROUTING_METHOD_OPTIONS,
  YES_NO_OPTIONS,
  YES_NO_SELECT_OPTIONS,
} from "../../../../../contants/common";
import FilterCard from "../../../../components/Cards/FilterCard";
import FormInput from "../../../../components/Inputs/FormInput";
import FormSelectBox from "../../../../components/Inputs/FormSelectBox";
import FormTextArea from "../../../../components/Inputs/FormTextArea";
import FreeOnBoardSelector from "../../../../components/Selectors/FreeOnBoardSelector";
import ProductUnitUomSelector from "../../../../components/Selectors/ProductUnitUomSelector";
import ShipmentProductClassSelector from "../../../../components/Selectors/ShipmentProductClassSelector";
import ShipmentTermsSelector from "../../../../components/Selectors/ShipmentTermsSelector";
import WeightUomSelector from "../../../../components/Selectors/WeightUomSelector";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../utilz/i18n";

const TmsPovSettingsForm = () => {
  const [form] = Form.useForm();
  const { tmsSettings } = useSelector((state) => state.settings);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  useEffect(() => {
    if (tmsSettings?.success) {
      form.setFieldsValue(tmsSettings?.data?.povSettings);
    }
  }, [tmsSettings?.success]);

  const handlePovModuleChange = (data) => {
    if (!data || data.value === "N") {
      form.setFieldsValue({
        shipmentTermsAllowed: null,
        shipmentIdAlphaPrefix: null,
        numberOfRoutingCarrierReturned: null,
        povDestinationMethod: null,
        routingMethod: "NA",
        allowDraftShipments: null,
        defaultPackageUom: null,
        defaultUom: null,
        defaultFobCode: null,
        defaultClass: null,
        minimumDays: null,
        maximumDays: null,
        minimumDaysErrorMsg: null,
        maximumDaysErrorMsg: null,
      });
    } else {
      form.setFieldsValue({
        defaultClass: 70,
        minimumDays: 3,
        maximumDays: 365,
        minimumDaysErrorMsg: t(
          "translation:tms-settings.fields.minimumDaysError.message"
        ),
        maximumDaysErrorMsg: t(
          "translation:tms-settings.fields.maximumDaysError.message"
        ),
        shipmentIdAlphaPrefix: "",
      });
    }
  };

  const poValidationFlag = Form.useWatch("poValidationFlag", form);

  return (
    <Form form={form} name={"povSettings"} defaultValue={{}}>
      <Row gutter={30}>
        <Col md={12}>
          <FilterCard
            shadow={false}
            title={t("translation:tms-settings.defaultShipmentPageValues")}
            showBorder={true}
            className="mb-25"
          >
            <div className="p-15">
              <Row gutter={30}>
                <Col sm={12}>
                  <FormSelectBox
                    name="poValidationFlag"
                    label={t(
                      "translation:tms-settings.fields.turnONPOVModule.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.turnONPOVModule.placeholder"
                    )}
                    options={YES_NO_OPTIONS}
                    margin={"mb-15"}
                    onChange={handlePovModuleChange}
                    rules={[
                      {
                        required: true,
                        message: t(
                          "translation:tms-settings.fields.turnONPOVModule.message"
                        ),
                      },
                    ]}
                    tooltip={t(
                      "translation:tms-settings.fields.turnONPOVModule.tooltip"
                    )}
                  />
                  <FormInput
                    name="shipmentIdStartingInteger"
                    label={t(
                      "translation:tms-settings.fields.shipmentStartingNumber.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.shipmentStartingNumber.label"
                    )}
                    margin={
                      poValidationFlag === "Y" &&
                      !tmsSettings?.data?.povSettings?.poValidationFlag === "Y"
                        ? "mb-15"
                        : "mb-20"
                    }
                    editMode={
                      poValidationFlag === "Y" &&
                      !tmsSettings?.data?.povSettings?.poValidationFlag
                    }
                    tooltip={t(
                      "translation:tms-settings.fields.shipmentStartingNumber.tooltip"
                    )}
                  />
                  <FormInput
                    name="shipmentIdAlphaPrefix"
                    label={t(
                      "translation:tms-settings.fields.shipmentStartingNumber.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.shipmentStartingNumber.label"
                    )}
                    editMode={poValidationFlag === "Y"}
                    margin={"mb-15"}
                    tooltip={t(
                      "translation:tms-settings.fields.shipmentStartingNumber.tooltip"
                    )}
                  />
                  <FormInput
                    name="numberOfRoutingCarrierReturned"
                    label={t(
                      "translation:tms-settings.fields.noRoutingGuideCarriersReturned.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.noRoutingGuideCarriersReturned.label"
                    )}
                    inputType={"number"}
                    margin={"mb-15"}
                    editMode={poValidationFlag === "Y"}
                    tooltip={t(
                      "translation:tms-settings.fields.noRoutingGuideCarriersReturned.tooltip"
                    )}
                  />
                  <FormSelectBox
                    name="isPoItemLocation"
                    label={"Po Item Location "}
                    placeholder={"Select the Po Item Location"}
                    options={YES_NO_SELECT_OPTIONS}
                    margin={"m-0"}
                    editMode={poValidationFlag === "Y"}
                    tooltip={
                      "If setting is set to ‘Yes’ than Vendor users will be allowed to create Po Item Location in POV module."
                    }
                  />
                </Col>
                {/* ------------------------------------------------- */}
                <Col sm={12} className={"d-flex flex-direction-column"}>
                  <ShipmentTermsSelector
                    name={"shipmentTermsAllowed"}
                    label={t(
                      "translation:tms-settings.fields.shipmentTermsAllowed.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.shipmentTermsAllowed.placeholder"
                    )}
                    margin={"mb-15"}
                    editMode={poValidationFlag === "Y"}
                    tooltip={t(
                      "translation:tms-settings.fields.shipmentTermsAllowed.tooltip"
                    )}
                  />
                  <FormSelectBox
                    name="povDestinationMethod"
                    label={t(
                      "translation:tms-settings.fields.povDestinationMethod.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.povDestinationMethod.placeholder"
                    )}
                    margin={"mb-15"}
                    options={TMS_SETTINGS_POV_DESTINATION_OPTIONS}
                    editMode={poValidationFlag === "Y"}
                    rules={[
                      {
                        required: poValidationFlag === "Y",
                        message: t(
                          "translation:tms-settings.fields.povDestinationMethod.message"
                        ),
                      },
                    ]}
                    tooltip={t(
                      "translation:tms-settings.fields.povDestinationMethod.tooltip"
                    )}
                  />

                  <FormSelectBox
                    name="routingMethod"
                    label={t(
                      "translation:tms-settings.fields.routingMethod.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.routingMethod.placeholder"
                    )}
                    margin={"mb-15"}
                    options={TMS_SETTINGS_POV_ROUTING_METHOD_OPTIONS}
                    editMode={poValidationFlag === "Y"}
                    tooltip={t(
                      "translation:tms-settings.fields.routingMethod.tooltip"
                    )}
                  />

                  <FormSelectBox
                    name="allowDraftShipments"
                    label={t(
                      "translation:tms-settings.fields.allowDraftShipment.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.allowDraftShipment.placeholder"
                    )}
                    options={YES_NO_SELECT_OPTIONS}
                    margin={"mb-15"}
                    editMode={poValidationFlag === "Y"}
                    tooltip={t(
                      "translation:tms-settings.fields.allowDraftShipment.tooltip"
                    )}
                  />

                  <FormSelectBox
                    name="isCatchDate"
                    label={"CatchDate"}
                    placeholder={"Select the catch date"}
                    options={YES_NO_SELECT_OPTIONS}
                    margin={"m-0"}
                    editMode={poValidationFlag === "Y"}
                    tooltip={
                      "If setting is set to ‘Yes’ than Vendor users will be allowed to create Catch Date in POV module."
                    }
                  />
                </Col>
              </Row>
            </div>
          </FilterCard>

          <FilterCard
            shadow={false}
            title={t("translation:tms-settings.shipmentEntryPageSettings")}
            showBorder={true}
          >
            <div className="p-15">
              <Row gutter={30}>
                <Col sm={12}>
                  <ProductUnitUomSelector
                    isDefault={true}
                    name="defaultPackageUom"
                    label={t(
                      "translation:tms-settings.fields.packageUnitMeasure.label"
                    )}
                    margin={"mb-0"}
                    editMode={poValidationFlag === "Y"}
                    tooltip={t(
                      "translation:tms-settings.fields.packageUnitMeasure.tooltip"
                    )}
                  />
                </Col>
                <Col sm={12}>
                  <WeightUomSelector
                    isDefault={true}
                    name="defaultUom"
                    label={t(
                      "translation:tms-settings.fields.weightUnitMeasure.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.weightUnitMeasure.placeholder"
                    )}
                    margin={"mb-0"}
                    editMode={poValidationFlag === "Y"}
                    tooltip={t(
                      "translation:tms-settings.fields.weightUnitMeasure.tooltip"
                    )}
                  />
                </Col>
              </Row>
            </div>
          </FilterCard>
        </Col>
        <Col md={12}>
          <FilterCard
            shadow={false}
            title={t("translation:tms-settings.shipmentEntryPageSettings")}
            showBorder={true}
            className={"mb-25"}
          >
            <div className="p-15">
              <Row gutter={30}>
                <Col sm={12}>
                  <FreeOnBoardSelector
                    name="defaultFobCode"
                    label={t(
                      "translation:tms-settings.fields.freeOnBoardCode.label"
                    )}
                    editMode={poValidationFlag === "Y"}
                    margin={"mb-0"}
                    tooltip={t(
                      "translation:tms-settings.fields.freeOnBoardCode.tooltip"
                    )}
                  />
                </Col>
                <Col sm={12}>
                  <ShipmentProductClassSelector
                    name="defaultClass"
                    label={t(
                      "translation:tms-settings.fields.frightClass.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.frightClass.placeholder"
                    )}
                    editMode={poValidationFlag === "Y"}
                    margin={"mb-0"}
                    tooltip={t(
                      "translation:tms-settings.fields.frightClass.tooltip"
                    )}
                  />
                </Col>
              </Row>
            </div>
          </FilterCard>
          <FilterCard
            shadow={false}
            title={t("translation:tms-settings.shipmentEntryPageSettings")}
            showBorder={true}
          >
            <div className="p-15">
              <Row gutter={30}>
                <Col sm={12}>
                  <FormInput
                    name="minimumDays"
                    label={t(
                      "translation:tms-settings.fields.minimumDays.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.minimumDays.placeholder"
                    )}
                    editMode={poValidationFlag === "Y"}
                    margin={"mb-15"}
                    tooltip={t(
                      "translation:tms-settings.fields.minimumDays.tooltip"
                    )}
                  />
                </Col>
                <Col sm={12}>
                  <FormInput
                    name="maximumDays"
                    label={t(
                      "translation:tms-settings.fields.maximumDays.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.maximumDays.placeholder"
                    )}
                    editMode={poValidationFlag === "Y"}
                    margin={"mb-15"}
                    tooltip={t(
                      "translation:tms-settings.fields.maximumDays.tooltip"
                    )}
                  />
                </Col>
                <Col sm={24}>
                  <FormTextArea
                    name="minimumDaysErrorMsg"
                    label={t(
                      "translation:tms-settings.fields.minimumDaysError.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.minimumDaysError.placeholder"
                    )}
                    editMode={poValidationFlag === "Y"}
                    rows={2}
                    margin={"mb-15"}
                    tooltip={t(
                      "translation:tms-settings.fields.minimumDaysError.tooltip"
                    )}
                  />
                </Col>
                <Col sm={24}>
                  <FormTextArea
                    name="maximumDaysErrorMsg"
                    label={t(
                      "translation:tms-settings.fields.maximumDaysError.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.maximumDaysError.placeholder"
                    )}
                    editMode={poValidationFlag === "Y"}
                    margin={"m-0"}
                    rows={2}
                    tooltip={t(
                      "translation:tms-settings.fields.maximumDaysError.tooltip"
                    )}
                  />
                </Col>
              </Row>
            </div>
          </FilterCard>
        </Col>
      </Row>
    </Form>
  );
};

export default TmsPovSettingsForm;
