import { Col, Form, Row } from "antd";
import React, { useEffect, useMemo } from "react";
import { useSelector } from "react-redux";
import {
  TMS_SETTINGS_AUCTION_TYPE_OPTIONS,
  YES_NO_OPTIONS,
} from "../../../../../contants/common";
import FormInput from "../../../../components/Inputs/FormInput";
import FormSelectBox from "../../../../components/Inputs/FormSelectBox";
import CargoTypeSelector from "../../../../components/Selectors/CargoTypeSelector";
import EquipmentTypeSelector from "../../../../components/Selectors/EquipmentTypeSelector";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../../utilz/i18n';

const TmsAuctionSettingsForm = () => {
  const [form] = Form.useForm();
  const { tmsSettings } = useSelector((state) => state.settings);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  useEffect(() => {
    if (tmsSettings?.success) {
      let res = {
        ...tmsSettings?.data?.auctionSettings,
      }
      if(res.defaultEquipmentType === 0){
        res.defaultEquipmentType = '';
      }
      form.setFieldsValue(res);
    }
  }, [tmsSettings?.success]);

  const disableExpiredBids = Form.useWatch("disableExpiredBids", form);

  useEffect(() => {
    if (disableExpiredBids === "N") {
      form.setFieldValue("bidExpiryNoOfDays", null);
    }
  }, [disableExpiredBids]);

  return (
    <Form
      form={form}
      name={"auctionSettings"}
      defaultValue={{
        defaultEquipmentType:
          tmsSettings?.data?.auctionSettings?.defaultEquipmentType === 0
            ? ""
            : tmsSettings?.data?.auctionSettings?.defaultEquipmentType,
      }}
    >
      <Row gutter={30}>
        <Col md={6}>
          <EquipmentTypeSelector
            isDefault={true}
            name="defaultEquipmentType"
            label={t('translation:tms-settings.fields.defaultEquipmentType.label')}
            placeholder={t('translation:default.fields.equipmentType.label')}
            margin={"mb-15"}
            tooltip={t('translation:tms-settings.fields.defaultEquipmentType.tooltip')}
          />
          <CargoTypeSelector
            isDefault={true}
            name="defaultCargoType"
            label={t('translation:tms-settings.fields.defaultCargoType.label')}
            placeholder={t('translation:tms-settings.fields.defaultCargoType.placeholder')}
            margin={"m-0"}
            tooltip={t('translation:tms-settings.fields.defaultCargoType.tooltip')}
          />
        </Col>
        <Col md={6}>
          <FormSelectBox
            name="disableExpiredBids"
            label={t('translation:tms-settings.fields.populateBidExpiryDate.label')}
            placeholder={t('translation:tms-settings.fields.populateBidExpiryDate.placeholder')}
            options={YES_NO_OPTIONS}
            margin={"mb-15"}
            tooltip={t('translation:tms-settings.fields.populateBidExpiryDate.tooltip')}
          />
          <FormInput
            name="bidExpiryNoOfDays"
            label={t('translation:tms-settings.fields.addDaysBidExpiryDate.label')}
            placeholder={t('translation:tms-settings.fields.addDaysBidExpiryDate.placeholder')}
            inputType={"number"}
            editMode={disableExpiredBids === "Y"}
            margin={"m-0"}
            tooltip={t('translation:tms-settings.fields.addDaysBidExpiryDate.tooltip')}
          />
        </Col>
        <Col md={6}>
          <FormSelectBox
            name="isReverseAuction"
            label={t('translation:tms-settings.fields.auctionType.label')}
            placeholder={t('translation:tms-settings.fields.auctionType.placeholder')}
            options={TMS_SETTINGS_AUCTION_TYPE_OPTIONS}
            margin={"mb-15"}
            tooltip={t('translation:tms-settings.fields.auctionType.tooltip')}
          />
        </Col>
        <Col md={6}>
          <FormInput
            name="maxBidHours"
            label={t('translation:tms-settings.fields.maximumBidHours.maximumBidHours')}
            placeholder={t('translation:tms-settings.fields.maximumBidHours.maximumBidHours')}
            inputType={"number"}
            editMode={true}
            margin={"mb-15"}
            rules={[
              {
                required: true,
                message: t('translation:tms-settings.fields.maximumBidHours.message'),
              },
              () => ({
                validator(_, value) {
                  if (value <= 2147483647) {
                    return Promise.resolve();
                  }
                  return Promise.reject(
                    new Error(
                     t('translation:tms-settings.fields.maximumBidHours.error')
                    )
                  );
                },
              }),
            ]}
            tooltip={t('translation:tms-settings.fields.maximumBidHours.tooltip')}
          />
        </Col>
      </Row>
    </Form>
  );
};

export default TmsAuctionSettingsForm;
