import { Col, Form, Row } from 'antd';
import React, { useState, useMemo } from 'react';
import Paper from '../../components/Paper/Paper';
import Button from '../../components/Buttons/Button';
import TmsSettingsScreen from '../../screens/Settings/TMS-Settings/TmsSettingsScreen';
import PrimaryLabel from '../../components/Labels/PrimaryLabel';
import { useDispatch, useSelector } from 'react-redux';
import { saveTmsSettingsInProgress } from '../../../redux/settings/SettingsActions';
import { convertTmsSettingsLocalToAPI } from '../../../utilz/settingsUtils';
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../utilz/i18n';
import { formTabKeys } from '../../../configs';

const TmsSettingsPage = () => {
  const { pathname: activeTab} = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);
  const { selectedCompany, user } = useSelector((state) => state.auth);
  const { saveTmsSettings, tmsSettings } = useSelector(
    (state) => state.settings
  );
  const dispatch = useDispatch();
  const [submitForm] = Form.useForm();
  const [restForm] = Form.useForm();
  const [currentTab, setCurrentTab] = useState(['1']);

  const handleSubmitForm = async (triggeredForm, { forms }) => {
    try {
      if (triggeredForm === 'submitForm') {
        let formResponse = await Promise.all([
          validateForm(forms.companySettings, 'companySettings'),
          validateForm(forms.shipmentSettings, 'shipmentSettings'),
          validateForm(forms.auctionSettings, 'auctionSettings'),
          validateForm(forms.povSettings, 'povSettings'),
        ]);
        formResponse = formResponse.reduce((a, b) => {
          return { ...a, ...b };
        }, {});

        let payload = {
          ...formResponse.companySettings,
          ...formResponse.shipmentSettings,
          ...formResponse.auctionSettings,
          ...formResponse.povSettings,
          createdBy: user?.data?.id,
          customerKey: selectedCompany?.companyId,
        };
        payload = convertTmsSettingsLocalToAPI(payload);
        dispatch(saveTmsSettingsInProgress(payload));
      } else {
        handleReset(forms);
      }
    } catch (error) {
      setCurrentTab([formTabKeys[error.formName]]);
    }
  };

  const validateForm = (formInstance, formName) => {
    return new Promise(async (resolve, reject) => {
      try {
        const validationStatus = await formInstance.validateFields();
        resolve({ [formName]: validationStatus });
      } catch (error) {
        reject({ formName, ...error });
      }
    });
  };

  const handleReset = (forms) => {
    forms.companySettings.setFieldsValue(tmsSettings.data.companySettings);
    forms.shipmentSettings.setFieldsValue(tmsSettings.data.shipmentSettings);
    forms.auctionSettings.setFieldsValue(tmsSettings.data.auctionSettings);
    forms.povSettings.setFieldsValue(tmsSettings.data.povSettings);
  };

  return (
    <Form.Provider
      className={'overflow-hidden h-100'}
      onFormFinish={handleSubmitForm}
    >
      <Row
        className={'flex-direction-column flex-nowrap overflow-hidden h-100'}
      >
        <Col className={'mb-15'}>
          <Paper className={'p-15 border-radius-bottom-none'}>
            <Row align='middle' justify='space-between'>
              <Col>
                <PrimaryLabel className='dark-header-text'>
                  {t('translation:default.navMenu.tmsSettings')}
                </PrimaryLabel>
              </Col>
              <Col>
                <Row gutter={16} justify='end' align={'middle'}>
                  <Col>
                    <Form form={submitForm} name='submitForm'>
                      <Button
                        variant='success'
                        className={''}
                        htmlType='submit'
                        loading={saveTmsSettings?.progressing}
                      >
                        {t('translation:default.buttons.save')}
                      </Button>
                    </Form>
                  </Col>
                  <Col>
                    <Form form={restForm} name='resetForm'>
                      <Button
                        type='default'
                        variant='danger'
                        className={''}
                        htmlType='submit'
                        disabled={saveTmsSettings?.progressing}
                      >
                        {t('translation:default.buttons.restoreToDefault')}
                      </Button>
                    </Form>
                  </Col>
                </Row>
              </Col>
            </Row>
          </Paper>
        </Col>
        <Col className='flex-1 custom-scroll-bar'>
          <TmsSettingsScreen
            activeTab={currentTab}
            setActiveTab={setCurrentTab}
          />
        </Col>
      </Row>
    </Form.Provider>
  );
};

export default TmsSettingsPage;
