import { Col, Form, Row } from "antd";
import React, { useEffect, useMemo } from "react";
import { useSelector } from "react-redux";
import {
  PRICING_TYPES_OPTIONS,
  TMS_SETTINGS_SPOT_BID_OPTIONS,
  YES_NO_OPTIONS,
  YES_NO_SELECT_OPTIONS,
  YES_NO_SELECT_OPTIONS_EMPTY,
} from "../../../../../contants/common";
import FilterCard from "../../../../components/Cards/FilterCard";
import FormInput from "../../../../components/Inputs/FormInput";
import FormSelectBox from "../../../../components/Inputs/FormSelectBox";
import FormTextArea from "../../../../components/Inputs/FormTextArea";
import HiddenFormInput from "../../../../components/Inputs/HiddenFormInput";
import CargoTypeSelector from "../../../../components/Selectors/CargoTypeSelector";
import IncotermSelector from "../../../../components/Selectors/IncotermSelector";
import RatingPreferenceSelector from "../../../../components/Selectors/RatingPreferenceSelector";
import ShipmentBranchSelector from "../../../../components/Selectors/ShipmentBranchSelector";
import ShipmentProductClassSelector from "../../../../components/Selectors/ShipmentProductClassSelector";
import ShipmentTermsSelector from "../../../../components/Selectors/ShipmentTermsSelector";
import ShipmentTypeSelector from "../../../../components/Selectors/ShipmentTypeSelector";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../utilz/i18n";

const TmsShipmentSettingsForm = () => {
  const [form] = Form.useForm();
  const { tmsSettings } = useSelector((state) => state.settings);
  const { selectedCompany } = useSelector((state) => state.auth);

  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);

  useEffect(() => {
    if (tmsSettings?.success) {
      form.setFieldsValue({
        ...tmsSettings?.data?.shipmentSettings,
        defaultBranchKey:
          tmsSettings?.data?.shipmentSettings?.defaultBranchKey == 0
            ? undefined
            : Number(tmsSettings?.data?.shipmentSettings?.defaultBranchKey),
      });
    }
  }, [tmsSettings?.success]);

  const defaultRatingPreferencevalue = Form.useWatch(
    "defaultRatingPreferencevalue",
    form
  );

  useEffect(() => {
    if (defaultRatingPreferencevalue) {
      form.setFieldValue("isDefaultRatingPreference", "Y");
    } else {
      form.setFieldValue("isDefaultRatingPreference", "N");
    }
  }, [defaultRatingPreferencevalue]);

  return (
    <Form form={form} name={"shipmentSettings"}>
      <FilterCard
        className="mb-15"
        shadow={false}
        title={t("translation:tms-settings.generalSettings")}
        showBorder={true}
      >
        <div className="px-15 pt-15">
          <Row gutter={30}>
            <Col sm={6}>
              <FormInput
                name="nextID"
                label={t(
                  "translation:tms-settings.fields.shipmentNoCounter.label"
                )}
                editMode={
                  tmsSettings?.data?.companySettings?.statusInTMS === "N"
                }
                margin={"mb-15"}
                rules={[
                  {
                    required: true,
                    message: t(
                      "translation:tms-settings.fields.shipmentNoCounter.message"
                    ),
                  },
                  () => ({
                    validator(_, value) {
                      if (String(value).length === 8) {
                        return Promise.resolve();
                      }
                      return Promise.reject(
                        new Error(
                          t(
                            "translation:tms-settings.fields.shipmentNoCounter.error"
                          )
                        )
                      );
                    },
                  }),
                ]}
                inputType={"number"}
                tooltip={t(
                  "translation:tms-settings.fields.shipmentNoCounter.tooltip"
                )}
              />
            </Col>
            <Col sm={6}>
              <FormSelectBox
                name="enableMasterOrders"
                label={t(
                  "translation:tms-settings.fields.masterChildShipment.label"
                )}
                placeholder={t(
                  "translation:tms-settings.fields.masterChildShipment.placeholder"
                )}
                options={YES_NO_OPTIONS}
                margin={"mb-15"}
                tooltip={t(
                  "translation:tms-settings.fields.masterChildShipment.tooltip"
                )}
              />
            </Col>
            <Col sm={6}>
              <FormSelectBox
                name="pricingType"
                options={PRICING_TYPES_OPTIONS}
                label={t("translation:default.fields.pricingType.label")}
                placeholder={t(
                  "translation:default.fields.pricingType.message"
                )}
                margin={"mb-15"}
                rules={[
                  {
                    required: true,
                    message: t(
                      "translation:default.fields.pricingType.message"
                    ),
                  },
                ]}
                tooltip={t(
                  "translation:tms-settings.fields.pricingType.tooltip"
                )}
              />
            </Col>
            <Col sm={6}>
              <FormSelectBox
                name="isvisiablePaidBy"
                label={t("translation:tms-settings.fields.displayPaidBY.label")}
                placeholder={t(
                  "translation:tms-settings.fields.displayPaidBY.placeholder"
                )}
                options={YES_NO_OPTIONS}
                margin={"mb-15"}
                tooltip={t(
                  "translation:tms-settings.fields.displayPaidBY.tooltip"
                )}
              />
            </Col>
            <Col sm={6}>
              <FormSelectBox
                name="shipmentApprovalRequired"
                label={t(
                  "translation:tms-settings.fields.shipmentApprovalProcess.label"
                )}
                placeholder={t(
                  "translation:tms-settings.fields.shipmentApprovalProcess.placeholder"
                )}
                options={YES_NO_OPTIONS}
                margin={"m-0"}
                tooltip={t(
                  "translation:tms-settings.fields.shipmentApprovalProcess.tooltip"
                )}
              />
            </Col>
            <Col sm={6}>
              <FormSelectBox
                name="isCategoryToValidate"
                label={t(
                  "translation:tms-settings.fields.validateCategoryDetails.label"
                )}
                placeholder={t(
                  "translation:tms-settings.fields.validateCategoryDetails.placeholder"
                )}
                options={YES_NO_OPTIONS}
                margin={"m-0"}
                tooltip={t(
                  "translation:tms-settings.fields.validateCategoryDetails.tooltip"
                )}
              />
            </Col>
            <Col sm={6}>
              <FormSelectBox
                name="isWeightCalculation"
                label={t(
                  "translation:tms-settings.fields.calculateChargeableWeight.label"
                )}
                placeholder={t(
                  "translation:tms-settings.fields.calculateChargeableWeight.placeholder"
                )}
                options={YES_NO_OPTIONS}
                margin={"m-0"}
                tooltip={t(
                  "translation:tms-settings.fields.calculateChargeableWeight.tooltip"
                )}
              />
            </Col>
            <Col sm={6}>
              <FormSelectBox
                name="routingGuideMatch"
                label={t(
                  "translation:tms-settings.fields.routingGuideMatch.label"
                )}
                placeholder={t(
                  "translation:tms-settings.fields.routingGuideMatch.placeholder"
                )}
                options={YES_NO_SELECT_OPTIONS}
                margin={"m-0"}
                tooltip={t(
                  "translation:tms-settings.fields.routingGuideMatch.tooltip"
                )}
              />
            </Col>
            <Col sm={6}>
              <FormSelectBox
                name="isCategoryVisible"
                label={t(
                  "translation:tms-settings.fields.shipmentCategoryVisible.label"
                )}
                placeholder={t(
                  "translation:tms-settings.fields.shipmentCategoryVisible.placeholder"
                )}
                options={YES_NO_OPTIONS}
                tooltip={t(
                  "translation:tms-settings.fields.shipmentCategoryVisible.tooltip"
                )}
              />
            </Col>
            <Col sm={6}>
              <FormSelectBox
                name="isBillToVisible"
                label={t(
                  "translation:tms-settings.fields.shipmentBillToVisible.label"
                )}
                placeholder={t(
                  "translation:tms-settings.fields.shipmentBillToVisible.placeholder"
                )}
                options={YES_NO_OPTIONS}
                tooltip={t(
                  "translation:tms-settings.fields.shipmentBillToVisible.tooltip"
                )}
              />
            </Col>
            <Col sm={6}>
              <FormSelectBox
                name="isVendorVisible"
                label={t(
                  "translation:tms-settings.fields.shipmentVendorVisible.label"
                )}
                placeholder={t(
                  "translation:tms-settings.fields.shipmentVendorVisible.placeholder"
                )}
                options={YES_NO_OPTIONS}
                tooltip={t(
                  "translation:tms-settings.fields.shipmentVendorVisible.tooltip"
                )}
              />
            </Col>
          </Row>
        </div>
      </FilterCard>
      <Row gutter={30}>
        <Col md={12}>
          <FilterCard
            shadow={false}
            title={t("translation:tms-settings.defaultShipmentPageValues")}
            showBorder={true}
            className="mb-25"
          >
            <div className="p-15">
              <Row gutter={30}>
                <Col sm={12}>
                  <ShipmentBranchSelector
                    name="defaultBranchKey"
                    label={t("translation:default.fields.branch.label")}
                    placeholder={t(
                      "translation:default.fields.branch.placeholder"
                    )}
                    customerId={selectedCompany?.companyId}
                    margin={"mb-15"}
                    rules={[
                      {
                        required: true,
                        message: t("translation:default.fields.branch.message"),
                      },
                    ]}
                    tooltip={t(
                      "translation:tms-settings.fields.branch.tooltip"
                    )}
                  />
                  <IncotermSelector
                    isDefault={true}
                    name="defaultIncoTerm"
                    label={t("translation:default.fields.incoTerm.label")}
                    placeholder={t(
                      "translation:default.fields.incoTerm.placeholder"
                    )}
                    margin={"mb-15"}
                    tooltip={t("translation:default.fields.incoTerm.tooltip")}
                  />
                  <FormSelectBox
                    name="defaultSpotBidEndStatus"
                    label={t(
                      "translation:tms-settings.fields.defaultSpotBidEndStatus.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.defaultSpotBidEndStatus.placeholder"
                    )}
                    options={TMS_SETTINGS_SPOT_BID_OPTIONS}
                    margin={"mb-15"}
                    tooltip={t(
                      "translation:tms-settings.fields.defaultSpotBidEndStatus.tooltip"
                    )}
                  />
                  <FormSelectBox
                    name="displayPO_SOOnShipmentLevel"
                    label={t(
                      "translation:tms-settings.fields.displayPOSOShipmentLevel.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.displayPOSOShipmentLevel.placeholder"
                    )}
                    options={YES_NO_SELECT_OPTIONS_EMPTY}
                    margin={"mb-15"}
                    tooltip={t(
                      "translation:tms-settings.fields.displayPOSOShipmentLevel.tooltip"
                    )}
                  />
                  <FormInput
                    name="defaultProductCode"
                    label={t("translation:default.fields.productCode.label")}
                    placeholder={t(
                      "translation:default.fields.productCode.placeholder"
                    )}
                    editMode={true}
                    margin={"mb-15"}
                    tooltip={t(
                      "translation:default.fields.productCode.tooltip"
                    )}
                  />
                  <FormInput
                    name="defaultProdDesc"
                    label={t("translation:default.fields.productDesc.label")}
                    placeholder={t(
                      "translation:default.fields.productDesc.enter"
                    )}
                    editMode={true}
                    margin={"mb-15"}
                    tooltip={t(
                      "translation:default.fields.productDesc.tooltip"
                    )}
                  />
                  <ShipmentTermsSelector
                    isDefault={true}
                    name={"defaultShipmentTerms"}
                    label={t("translation:default.fields.shipmentTerms.label")}
                    placeholder={t(
                      "translation:default.fields.shipmentTerms.message"
                    )}
                    margin={"mb-15"}
                    tooltip={t(
                      "translation:default.fields.shipmentTerms.tooltip"
                    )}
                  />
                  <ShipmentTypeSelector
                    isDefault={true}
                    name={"defaultShipmentType"}
                    label={t(
                      "translation:tms-settings.fields.shipmentTypes.label"
                    )}
                    placeholder={t(
                      "translation:default.fields.shipmentType.message"
                    )}
                    margin={"mb-15"}
                    tooltip={t(
                      "translation:tms-settings.fields.shipmentTypes.tooltip"
                    )}
                  />
                  <FormSelectBox
                    name="isShowRatingDetails"
                    label={t(
                      "translation:tms-settings.fields.displayRatingDetailsAccordion.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.displayRatingDetailsAccordion.placeholder"
                    )}
                    options={YES_NO_SELECT_OPTIONS_EMPTY}
                    margin={"mb-5"}
                    tooltip={t(
                      "translation:tms-settings.fields.displayRatingDetailsAccordion.tooltip"
                    )}
                  />
                </Col>
                <Col sm={12} className={"d-flex flex-direction-column"}>
                  <FormSelectBox
                    name="defaultHazmat"
                    label={t("translation:tms-settings.fields.hazmat.label")}
                    placeholder={t(
                      "translation:tms-settings.fields.hazmat.placeholder"
                    )}
                    margin={"mb-15"}
                    options={YES_NO_SELECT_OPTIONS_EMPTY}
                    tooltip={t(
                      "translation:tms-settings.fields.hazmat.tooltip"
                    )}
                  />
                  <RatingPreferenceSelector
                    isDefault={true}
                    name="defaultRatingPreferencevalue"
                    label={t(
                      "translation:tms-settings.fields.ratingPreference.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.ratingPreference.placeholder"
                    )}
                    margin={"mb-15"}
                    tooltip={t(
                      "translation:tms-settings.fields.ratingPreference.tooltip"
                    )}
                  />
                  <ShipmentProductClassSelector
                    isDefault={true}
                    name="defaultFreightClass"
                    label={t("translation:default.fields.freightClass.label")}
                    placeholder={t(
                      "translation:default.fields.freightClass.placeholder"
                    )}
                    margin={"mb-15"}
                    tooltip={t(
                      "translation:default.fields.freightClass.tooltip"
                    )}
                  />
                  <FormInput
                    name="defaultLen"
                    label={t("translation:default.fields.length")}
                    placeholder={t("translation:default.fields.length")}
                    editMode={true}
                    margin={"mb-15"}
                    tooltip={t(
                      "translation:tms-settings.fields.length.tooltip"
                    )}
                  />
                  <FormInput
                    name="defaultWidth"
                    label={t("translation:default.fields.width")}
                    placeholder={t("translation:default.fields.width")}
                    editMode={true}
                    margin={"mb-15"}
                    tooltip={t("translation:tms-settings.fields.width.tooltip")}
                  />
                  <FormInput
                    name="defaultHeight"
                    label={t("translation:default.fields.height")}
                    placeholder={t("translation:default.fields.height")}
                    editMode={true}
                    margin={"mb-15"}
                    tooltip={t(
                      "translation:tms-settings.fields.height.tooltip"
                    )}
                  />
                  <FormTextArea
                    name="defaultBusinessJustification"
                    label={t(
                      "translation:tms-settings.fields.defaultInternalNotes.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.defaultInternalNotes.placeholder"
                    )}
                    editMode={true}
                    rows={2}
                    enableAutoFocus={false}
                    fillHeight={true}
                    margin={"mb-5"}
                    tooltip={t(
                      "translation:tms-settings.fields.defaultInternalNotes.label"
                    )}
                  />
                </Col>
              </Row>
            </div>
          </FilterCard>
          <FilterCard
            shadow={false}
            title={t("translation:tms-settings.shipmentApprovalProcess")}
            showBorder={true}
          >
            <div className="p-15">
              <Row gutter={30}>
                <Col sm={12}>
                  <FormInput
                    name="shipmentApprovalCount"
                    label={t(
                      "translation:tms-settings.fields.shipmentApprovalCount.label"
                    )}
                    editMode={false}
                    margin={"mb-5"}
                    tooltip={t(
                      "translation:tms-settings.fields.shipmentApprovalCount.tooltip"
                    )}
                  />
                </Col>
                <Col sm={12}>
                  <FormSelectBox
                    name="isRequiredApprovalNumber"
                    label={t(
                      "translation:tms-settings.fields.requireApprovalNumber.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.requireApprovalNumber.placeholder"
                    )}
                    options={YES_NO_OPTIONS}
                    editMode={false}
                    margin={"mb-5"}
                    tooltip={t(
                      "translation:tms-settings.fields.requireApprovalNumber.tooltip"
                    )}
                  />
                </Col>
              </Row>
            </div>
          </FilterCard>
        </Col>
        <Col md={12}>
          <FilterCard
            shadow={false}
            title={t("translation:tms-settings.emailDistribution")}
            showBorder={true}
            className={"mb-25"}
          >
            <div className="p-15">
              <FormInput
                name="adminEmail"
                label={t("translation:tms-settings.fields.adminEmail.label")}
                editMode={true}
                margin={"mb-15"}
                rules={[
                  {
                    required: true,
                    message: t(
                      "translation:tms-settings.fields.adminEmail.messageRequired"
                    ),
                  },
                  {
                    type: "email",
                    message: t("translation:tms-settings.fields.emailMessage"),
                  },
                ]}
                tooltip={t(
                  "translation:tms-settings.fields.adminEmail.tooltip"
                )}
              />
              <FormInput
                name="shipmentSupportMail"
                label={t("translation:tms-settings.fields.supportEmail.label")}
                editMode={true}
                margin={"mb-15"}
                rules={[
                  {
                    required: true,
                    message: t(
                      "translation:tms-settings.fields.supportEmail.messageRequired"
                    ),
                  },
                  {
                    type: "email",
                    message: t("translation:tms-settings.fields.emailMessage"),
                  },
                ]}
                tooltip={t(
                  "translation:tms-settings.fields.supportEmail.tooltip"
                )}
              />
              <FormInput
                name="emailFrom"
                label={t("translation:tms-settings.fields.fromEmail.label")}
                editMode={true}
                margin={"mb-15"}
                rules={[
                  {
                    required: true,
                    message: t(
                      "translation:tms-settings.fields.fromEmail.messageRequired"
                    ),
                  },
                  {
                    type: "email",
                    message: t("translation:tms-settings.fields.emailMessage"),
                  },
                ]}
                tooltip={t("translation:tms-settings.fields.fromEmail.tooltip")}
              />
              <FormSelectBox
                name="shipmentEmailAttachment"
                label={t(
                  "translation:tms-settings.fields.attachSupportingDoc.label"
                )}
                placeholder={t(
                  "translation:tms-settings.fields.attachSupportingDoc.placeholder"
                )}
                options={YES_NO_OPTIONS}
                editMode={true}
                margin={"mb-0"}
                tooltip={t(
                  "translation:tms-settings.fields.attachSupportingDoc.tooltip"
                )}
              />
            </div>
          </FilterCard>
          <FilterCard
            shadow={false}
            title={t("translation:tms-settings.additionalSettings")}
            showBorder={true}
            className={"mb-25"}
          >
            <div className="p-15">
              <FormInput
                name="qtyTolerancePercentage"
                label={t("translation:tms-settings.fields.childShipment.label")}
                // editMode={true}
                margin={"mb-0"}
                rules={[
                  {
                    required: true,
                    message: t(
                      "translation:tms-settings.fields.childShipment.message"
                    ),
                  },
                ]}
                inputType={"number"}
                tooltip={t(
                  "translation:tms-settings.fields.childShipment.tooltip"
                )}
              />
            </div>
          </FilterCard>
          <FilterCard
            shadow={false}
            title={t("translation:tms-settings.autoRouteProcess")}
            showBorder={true}
          >
            <div className="p-15">
              <Row gutter={30}>
                <Col md={12}>
                  <FormSelectBox
                    name="autoRate"
                    label={t(
                      "translation:tms-settings.fields.autoRateProcess.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.autoRateProcess.placeholder"
                    )}
                    options={YES_NO_OPTIONS}
                    editMode={true}
                    margin={"mb-15"}
                    tooltip={t(
                      "translation:tms-settings.fields.autoRateProcess.tooltip"
                    )}
                  />
                  <FormSelectBox
                    name="autoTender"
                    label={t(
                      "translation:tms-settings.fields.autoTenderProcess.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.autoTenderProcess.placeholder"
                    )}
                    options={YES_NO_OPTIONS}
                    editMode={true}
                    margin={"mb-15"}
                    tooltip={t(
                      "translation:tms-settings.fields.autoTenderProcess.tooltip"
                    )}
                  />
                  <FormInput
                    name="autoTenderAmount"
                    label={t(
                      "translation:tms-settings.fields.maxAutoTenderAmount.label"
                    )}
                    placeholder={t(
                      "translation:tms-settings.fields.maxAutoTenderAmount.placeholder"
                    )}
                    editMode={true}
                    margin={"mb-0"}
                    tooltip={t(
                      "translation:tms-settings.fields.maxAutoTenderAmount.tooltip"
                    )}
                  />
                </Col>
                <Col md={12}>
                  <FormSelectBox
                    name="recalculateFreightClass"
                    label={t(
                      "translation:tms-settings.fields.recalculateFreightClass.label"
                    )}
                    placeholder={t(
                      "translation:default.fields.freightClass.placeholder"
                    )}
                    options={YES_NO_OPTIONS}
                    editMode={true}
                    margin={"mb-15"}
                    tooltip={t(
                      "translation:tms-settings.fields.recalculateFreightClass.tooltip"
                    )}
                  />
                  <CargoTypeSelector
                    name="autoTenderCargoType"
                    label={t(
                      "translation:tms-settings.fields.autoTenderSpecificCargoType.label"
                    )}
                    placeholder={t(
                      "translation:default.fields.cargoType.label"
                    )}
                    options={YES_NO_OPTIONS}
                    editMode={true}
                    margin={"mb-0"}
                    tooltip={t(
                      "translation:tms-settings.fields.autoTenderSpecificCargoType.tooltip"
                    )}
                  />
                </Col>
              </Row>
            </div>
          </FilterCard>
        </Col>
      </Row>
      <HiddenFormInput name="isDefaultRatingPreference" />
    </Form>
  );
};

export default TmsShipmentSettingsForm;
