import { Col, Row } from "antd";
import React, { useMemo } from "react";
import SvgConverter from "../../../../components/Common/SvgConverter";
import PrimaryLabel from "../../../../components/Labels/PrimaryLabel";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../../utilz/i18n';

const TmsSettingsAccordionHeader = ({ Icon = false, title = "Title" }) => {
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  return (
    <Row gutter={15} align={"middle"}>
      <Col className="lh-0">
        <SvgConverter Icon={Icon} color={"black"} width={24} height={24} />
      </Col>
      <Col>
        <PrimaryLabel className={"text-dark f-16 fw-700"}>{title}</PrimaryLabel>
      </Col>
    </Row>
  );
};

export default TmsSettingsAccordionHeader;
