import { Col, Form, Row } from "antd";
import React, { useEffect, useMemo } from "react";
import { useSelector } from "react-redux";
import FormSelectBox from "../../../../components/Inputs/FormSelectBox";
import { useLocation } from "react-router-dom";
import { useTranslation } from 'react-i18next';
import { routeFileSelector } from '../../../../../utilz/i18n';

const TmsCompanySettingsForm = () => {
  const [form] = Form.useForm();
  const { tmsSettings } = useSelector((state) => state.settings);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, 'translation']);

  useEffect(() => {
    if (tmsSettings?.success) {
      form.setFieldsValue(tmsSettings?.data?.companySettings);
    }
  }, [tmsSettings?.success]);

  return (
    <Form form={form} name={"companySettings"} defaultValue={{}}>
      <Row gutter={30}>
        <Col md={6}>
          <FormSelectBox
            name="statusInTMS"
            rules={[{ required: true, message: t('translation:tms-settings.fields.statusInTMS.message') }]}
            label={t('translation:tms-settings.fields.statusInTMS.label')}
            placeholder={t('translation:tms-settings.fields.statusInTMS.placeholder')}
            options={[
              { label: t('translation:tms-settings.fields.statusInTMS.optionActive'), value: "Y" },
              { label: t('translation:tms-settings.fields.statusInTMS.optionInActive'), value: "N" },
            ]}
            margin={"mb-5"}
            tooltip={t('translation:tms-settings.fields.statusInTMS.tooltip')}
          />
        </Col>
        <Col md={6}>
          <FormSelectBox
            name="brokerageCustomer"
            rules={[{ required: true, message: t('translation:tms-settings.fields.brokerageCustomer.message') }]}
            label={t('translation:tms-settings.fields.brokerageCustomer.label')}
            placeholder={t('translation:tms-settings.fields.brokerageCustomer.placeholder')}
            options={[
              { label: t('translation:tms-settings.fields.brokerageCustomer.optionTMS'), value: "N" },
              { label: t('translation:tms-settings.fields.brokerageCustomer.optionBrokerage'), value: "Y" },
            ]}
            margin={"mb-5"}
            tooltip={t('translation:tms-settings.fields.brokerageCustomer.tooltip')}
          />
        </Col>
      </Row>
    </Form>
  );
};

export default TmsCompanySettingsForm;
