import React, { useEffect, useState } from "react";
import { gerFreeOnBoardEffect } from "../../../redux/shipment/ShipmentEffects";
import FormSelectBox from "../Inputs/FormSelectBox";
import i18n from 'i18next';

const FreeOnBoardSelector = ({
  placeholder = i18n.t('translation:default.fields.appointmentType.placeholder'),
  label =  i18n.t('translation:default.fields.appointmentType.label'),
  rules = [],
  name,
  editMode,
  margin,
  tooltip,
}) => {
  const [options, setOptions] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    getStatusList();
  }, []);

  const getStatusList = async () => {
    try {
      setLoading(true);
      const { data } = await gerFreeOnBoardEffect();
      let options = data.map((row) => ({
        label: row.codeDesc,
        value: row.codeId,
      }));
      setOptions(options);
      setLoading(false);
    } catch (error) {
      console.log(error);
      setOptions([]);
      setLoading(false);
    }
  };

  return (
    <FormSelectBox
      loading={loading}
      options={options}
      placeholder={placeholder}
      label={label}
      rules={rules}
      name={name}
      editMode={editMode}
      margin={margin}
      tooltip={tooltip}
    />
  );
};

export default FreeOnBoardSelector;
